<template>
  <div class="yjd-import-container" :style="'background-image:url(' + bg + ')'">
    <el-tabs v-model="activeName" class="demo-tabs">
      <el-tab-pane
        v-for="(item, index) in state.menus"
        :key="index"
        :label="item.name"
        :name="item.id"
      >
        <div v-if="index == 1">
          <el-button type="primary" @click="handerClicks" round>{{
            item.title
          }}</el-button>
          <input
            id="ImportFiles"
            type="file"
            name="file"
            multiple="multiple"
            accept=".shp,.dbf"
            style="display: none"
            @change="importShp2"
          />
          <span style="line-height: 28px; font-size: 8px"
            >（请同时上传shp与dbf格式文件）</span
          >
          <br />
          <span>shp编码格式</span>
          <el-select
            v-model="state.shpbm"
            size="small"
            style="width: 102px; margin-left: 17px"
          >
            <el-option label="GBK" value="GBK">GBK </el-option>
            <el-option label="UTF-8" value="UTF-8">UTF-8</el-option>
          </el-select>

          <yselect
           v-if="state.shplayer.length"
            @childChange="childChange"
            v-model="state.shplayer"
             item="import"
            style="height: 160px; margin-top: 12px"
          >
          </yselect>
        </div>
        <div v-else>
          <el-button type="primary" @click="handerClickj" round>{{
            item.title
          }}</el-button>
          <input
            id="ImportFilej"
            type="file"
            name="file"
            multiple="multiple"
            accept=".json,.geojson"
            style="display: none"
            @change="importJson2"
          />

          <br />
          <span>坐标系</span>
          <el-select
            v-model="state.zbx"
            size="small"
            style="width: 146px; margin-left: 17px"
          >
            <el-option label="4326" value="4326">4326 </el-option>
            <el-option label="3857" value="3857">3857</el-option>
            <el-option label="火星" value="火星">火星</el-option>
            <el-option label="百度" value="百度">百度</el-option>
          </el-select>
          <yselect
          v-if="state.jsonlayer.length"
            @childChange="childChange"
            v-model="state.jsonlayer"
            item="import"
            style="height: 160px; margin-top: 12px"
          >
          </yselect>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script setup>
import { onUnmounted, reactive, ref } from "vue";
import { importJson, importShp } from "@/mapUtils/import";
import yselect from "@/common/y-select.vue";
const bg = require("@/assets/bg/leftkuang.png");
const activeName = ref("json");
const state = reactive({
  zbx: "4326",
  shpbm: "GBK",
  jsonlayer: [],
  shplayer: [],
  menus: [
    {
      id: "json",
      name: "导入json",
      title: "导入",
      method: "importJson",
    },
    {
      id: "shp",
      name: "导入shp",
      method: "importShp",
      title: "导入",
    },
  ],
});

const handerClicks = () => {
  document.getElementById("ImportFiles").click();
};
const handerClickj = () => {
  document.getElementById("ImportFilej").click();
};
onUnmounted(() => {
  console.log(1);
});
const childChange = (v, item) => {
  let layer = viewer.getLayer(v.id);
  if (layer) {
    if (v.active) {
      layer.show = true;
    } else {
      layer.show = false;
    }
  }
};
const importJson2 = (e) => {
  let filename = e.target.files[0].name;
  let name = filename.split(".")[0];
  state.jsonlayer.push({ label: name, id: "json-" + name, active: true });
  importJson(e, "json-" + name);
};
const importShp2 = (e) => {
  let filename = e.target.files[0].name;
  let name = filename.split(".")[0];
  state.shplayer.push({ label: name, id: "shp-" + name, active: true });
  importShp(e, "shp-" + name);
};
</script>

