export const createS3MLayer=(id, url) =>{
    let layer = viewer.getLayer(id)
    if (layer) {
        layer.show = true
        viewer.flyToPosition('116.45793269658417,39.92711385557482,617.5967826044838,181.95219395269424,-21.286437065473546')
        return
    }
     layer = new DC.S3MLayer(id,url)
    viewer.addLayer(layer)
    viewer.flyToPosition('116.45793269658417,39.92711385557482,617.5967826044838,181.95219395269424,-21.286437065473546')

}