<template>
  <div class="yjd-statistic-container" :style="'background-image:url(' + bg + ')'">
    <div class="tree_title"><span>分析列表</span></div>

    <el-collapse>
      <el-collapse-item
        :title="item.label"
        v-for="(item, index) in state.layers"
        :key="index"
      >
   
        <yselect v-if="item.child.length" @childChange="childChange" v-model="item.child" style="height:auto;"> </yselect>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script setup>
import { reactive } from "vue";
import * as staistic from "../../../mapUtils/staistic";
import yselect from "@/common/y-select.vue";
const bg = require("@/assets/bg/leftkuang.png");
const state = reactive({
  layers: [{
    label: "场景分析",

    child: [
       {label: "日照分析",active:false, method: "sunAny"},
          {label: "等高线",active:false, method: "contourLine"},
           {label: "可视域分析",active:false, method: "viewAny"},
          
    ]
  }
  ]
});
const childChange = (v, item) => {
  if (v.active) {
     staistic[v.method].start()
  } else {
    staistic[v.method].stop()
  }
};
</script>

