<template>
  <div class="yjd-left-container" :style="'background-image:url(' + bg + ')'">
    <div class="tree_title"><span>图层</span></div>

    <el-collapse>
      <el-collapse-item
        :title="item.label"
        v-for="(item, index) in state.layers"
        :key="index"
      >
     
         <yselect v-if="item.child.length" @childChange="childChange" v-model="item.child" :item="item" style="height:auto;"> </yselect>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script setup>
import { reactive } from "vue";
import * as tileset from "@/mapUtils/layer/tileset";
import * as vector from "@/mapUtils/layer/vector";
import * as s3m from "@/mapUtils/layer/s3m";
import layers from "@/config/layers.json";
import yselect from "@/common/y-select.vue";
const bg = require("@/assets/bg/leftkuang.png");
const state = reactive({
  layers: layers,
});
const childChange = (v, item) => {
  if (v.active) {
    if (item.type === "tileset") {
      tileset[v.method](v.id, v.url);
    }else if(item.type === "vector"){
      vector[v.method](v.id, v.url)
    }else if(item.type === "s3m"){
      s3m[v.method](v.id, v.url)
    }
  } else {
    let layer = viewer.getLayer(v.id);
    if (layer) {
      layer.show = false;
       if(item.type === "s3m"){
         layer.remove()
       }
    }
   
  }
};
</script>

