<template>
  <div class="yjd-leftmeun-container">

    <ul>
      <li
        v-for="(v, key) in state.meuns"
        :key="key"
        @click="itemClick(v)"
        :style="
          v.checked
            ? 'background-color: rgb(75 84 84 / 47%);border-left: 2px solid rgb(12, 192, 228);'
            : ''
        "
      >
        <img :src="v.checked ? v.select : v.img" />
        <span :style="v.checked ? 'color:aqua' : ''">{{ v.label }}</span>
      </li>
    </ul>
  </div>
</template>

<script setup>
import { reactive } from "vue";
const emit = defineEmits(["changeMeun"]);
const state = reactive({
  meuns: [
    {
      label: "图层",
      img: require("@/assets/img/layers.png"),
      select: require("@/assets/img/layers_selected.png"),
      checked: false,
      type:"isLeftLayer"
    },
    {
      label: "工具",
      img: require("@/assets/img/specialTopic.png"),
      select: require("@/assets/img/specialTopic_selected.png"),
      checked: false,
       type:"isLefttools"
    },
    {
      label: "导入",
      img: require("@/assets/img/import.png"),
      select: require("@/assets/img/import_selected.png"),
      checked: false,
       type:"isLeftimport"
    },
    {
      label: "分屏",
      img: require("@/assets/img/splitScreen.png"),
      select: require("@/assets/img/splitScreen_selected.png"),
      checked: false,
        type:"isLeftsplit"
    },
    {
      label: "分析",
      img: require("@/assets/img/statistics.png"),
      select: require("@/assets/img/statistics_selected.png"),
      checked: false,
       type:"isLeftstatistic"
    },
    {
      label: "效果",
      img: require("@/assets/img/legend.png"),
      select: require("@/assets/img/legend_selected.png"),
      checked: false,
       type:"isLeftlegend"
    },
  ],
});
const itemClick = (item) => {
  item.checked = !item.checked;
  if(item.checked){
   state.meuns.forEach(ite=>{
    if(ite.type!==item.type){
      ite.checked=false
    }
   
   })
  }
  emit("changeMeun", item.type,item.checked);
};
</script>

