  position: absolute;
  top: 0;
  height: calc(100vh - 20px);
  width: 100px;
  left: 0px;
  background-color: rgb(32 39 40 / 47%);
  padding-top: 20px;
  color: white;
}
img {
  width: 18px;
  height: 18px;
  top: 3px;
  position: relative;
}
ul li {
  padding-left: 5px;
  line-height: 40px;
  height: 40px;
  cursor: pointer;
  border-left: 2px solid white;
}
ul li:hover {
  background-color: rgb(75 84 84 / 47%);
  border-left: 2px solid rgb(12, 192, 228);
}
ul li span {
  margin-left: 2px;
}
</style>
