

export const addXzq = (id, url) => {
    createVectorLayer(id, url,(item,layer)=>{
        let pts = item.positions.map((postion) => {
            postion.alt += 3000
            return postion;
        })
        let wall = new DC.Wall(pts)
        wall.setStyle({
            material:  new DC.WallTrailMaterialProperty({
                color: DC.Namespace.Cesium.Color.fromCssColorString('#00ffff').withAlpha(0.8),
                speed: 10,
                repeat: { x: 6, y: 3 }
              })
        })
        layer.addOverlay(wall)
    })
}
 function createVectorLayer(id, url,callback) {
    let layer = viewer.getLayer(id)
    if (layer) {
        layer.show = true
       // viewer.flyTo(viewer.getLayer(id).getOverlays()[0])
        return
    }
    layer = new DC.VectorLayer(id)
    viewer.addLayer(layer)
    const apiLayer = new DC.GeoJsonLayer(id, url, { clampToGround: true })
   apiLayer.eachOverlay((item) => {
        if (item.polygon) {
            let polygon = DC.Polygon.fromEntity(item)
            callback(polygon,layer)
        }
        if (item.polyline) {
            let polyline = DC.Polyline.fromEntity(item)
            callback(polyline,layer)
        }

        if (item.billboard) {
            let billboard = DC.Billboard.fromEntity(item)
            callback(billboard,layer)
        }
        if (item.point) {
            let point = DC.Point.fromEntity(item)
            callback(point,layer)
        }
    })
}
