<template>
  <div class="home0">
    <leftmeun @changeMeun="changeMeun" />
    <leftlayers v-show="state.isLeftLayer" />
    <lefttools v-show="state.isLefttools"/>
     <leftimport v-show="state.isLeftimport"/>

       <leftsplit v-if="state.isLeftsplit"/>
         <leftstatistic v-show="state.isLeftstatistic"/>
           <leftlegend v-show="state.isLeftlegend"/>
    
  </div>
</template>

<script setup>
import leftmeun from "../components/left/index.vue";
import lefttools from "../components/left/leftwidgets/tools.vue";
import leftimport from "../components/left/leftwidgets/import.vue";
import leftlayers from "../components/left/leftwidgets/layer.vue";
import leftsplit from "../components/left/leftwidgets/split.vue";
import leftstatistic from "../components/left/leftwidgets/statistic.vue";
import leftlegend from "../components/left/leftwidgets/legend.vue";
import { reactive } from "vue";
const state = reactive({
  isLeftLayer: false,
  isLefttools: false,
  isLeftimport: false,
  isLeftsplit: false,
  isLeftstatistic: false,
  isLeftlegend:false
});
const changeMeun = (n, checked) => {
  if (checked) {
    [
      "isLeftLayer",
      "isLefttools",
      "isLeftimport",
      "isLeftsplit",
      "isLeftstatistic",
      "isLeftlegend"
    ].forEach((item) => {
      if (item !== n) {
        state[item] = false;
      }
    });
  }
  state[n] = !state[n];
};
</script>

<style scoped>
.home0 {
  position: absolute;
  top: 0;
  height: 100%;
}
</style>
