//  import MeasurePolygon from "../../utils/MeasurePolygon";
export const pointMark = () => {
  let drawlayer = viewer.getLayer("draw");
  if (!drawlayer) {
    drawlayer = new DC.VectorLayer("draw");
    viewer.addLayer(drawlayer);
  }
  let plot = new DC.Plot(viewer);
  plot.draw(
    "point",
     (overlay) => {
      drawlayer.addOverlay(overlay)
     },
    {
      pixelSize: 10,
      color: DC.Color.RED, //颜色
  outlineColor: DC.Color.WHITE, //边框颜色
  outlineWidth: 1, //边框大小，
    }
  );
}
export const poygonArea = () => {
  let drawlayer = viewer.getLayer("draw");
  if (!drawlayer) {
    drawlayer = new DC.VectorLayer("draw");
    viewer.addLayer(drawlayer);
  }
  new DC.MeasurePolygon({ viewer: viewer, layer: drawlayer });
}
export const lineLength = () => {
  let drawlayer = viewer.getLayer("draw");
  if (!drawlayer) {
    drawlayer = new DC.VectorLayer("draw");
    viewer.addLayer(drawlayer);
  }
  new DC.MeasureLine({ viewer: viewer, layer: drawlayer });
}
export const clearAll = () => {
  viewer.getLayer("draw").clear()
}