import * as shapefile from "shapefile";
export const importJson=async(e,id)=>{
    let file = e.target.files[0];
    // 格式判断
    if (file.type !== "application/json") {
      reject("仅允许上传json文件");
    }
    // 检验是否支持FileRender
    if (typeof FileReader === "undefined") {
      reject("当前浏览器不支持FileReader");
    }
  
    let f = await importFilej(file).then((res) => {
      return res;
    });
    JSONtoEntitie({type: "FeatureCollection",features:f.features},id)
    e.target.value = null;
}
export const importShp=async(e,id)=>{
    var features = [];
  let shpfile = null,
    dbffile = null;
  for (var i = 0; i < e.target.files.length; i++) {
    let file = e.target.files[i];
    if (file.name.indexOf(".shp") > -1) {
      shpfile = file;
    } else if (file.name.indexOf(".dbf") > -1) {
      dbffile = file;
    }
  }
  if (!shpfile) {
    return console.log("未检测到shp格式文件");
  }
  if (!dbffile) {
    return console.log("未检测到dbf格式文件");
  }
  let shp = await importFile(shpfile).then((res) => {
    return res;
  });
  let dbf = await importFile(dbffile).then((res) => {
    return res;
  });
  shapefile
    .open(shp, dbf, { encoding: "GBK" })
    .then((source) => {
      source.read().then(function log(result) {
        if (result.done) {
            // console.log(features);
            JSONtoEntitie({type: "FeatureCollection",features:features},id)
        } else {
          features.push(result.value);
          return source.read().then(log);
        }
      });
    })
    .catch((error) => console.error(error.stack));
  e.target.value= null; 
}
const importFile = (file) => {
    return new Promise((resolve, reject) => {
      var reader = new FileReader();
      reader.readAsArrayBuffer(file);
      reader.onload = function (e) {
        resolve(e.target.result);
      };
    });
  };
  const importFilej = (file) => {
    return new Promise((resolve, reject) => {
      var reader = new FileReader();
      reader.readAsText(file);
      reader.onload = function (e) {
        resolve(JSON.parse(e.target.result));
      };
    });
  };

  const JSONtoEntitie=(geojson,id)=>{
    let layer = new DC.VectorLayer(id)
  viewer.addLayer(layer)
    const dataSource = new DC.Namespace.Cesium.GeoJsonDataSource("name");    // 创建并取名
    dataSource.load(geojson,{ clampToGround:true }).then(function (data) { 
        const entities = data.entities.values;
        entities.forEach(item => {
          if(item.point){
            let point = DC.Point.fromEntity(item)
            layer.addOverlay(point)
          }else if (item.polyline) {
            let polyline = DC.Polyline.fromEntity(item)
            layer.addOverlay(polyline)
          }else if (item.polygon) {
            let polygon = DC.Polygon.fromEntity(item)
            layer.addOverlay(polygon)
          }
        }); 
        
    })
  }