<template>
  <div class="child_box" :style="style">
    <div class="child_item" v-for="(v, i) in modelValue" :key="i">
      <el-checkbox v-model="v.active" @change="childChange(v)" />
      <span class="label">{{ v.label }}</span>
      <img v-if="item&&item=='import'" src="asstes/img/x.png" alt="" srcset="">
    </div>
  </div>
</template>
<script setup>
const emit = defineEmits(["childChange"]);
const props = defineProps(["modelValue", "item", "style"]);
const childChange = (v) => {
  emit("childChange", v, props.item);
};
</script>
<style  lang="scss">
.child_box::-webkit-scrollbar {
  width: 5px;
  height: 8px;
}
.child_box::-webkit-scrollbar-thumb {
  border-radius: 10px;
  height: 10px;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: #d0ddef;
}
.child_box::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0);
  border-radius: 10px;
}
.child_box {
  align-items: center;
  background-color: rgb(77 80 80 / 50%);
  overflow-y: auto;
    padding: 5px 10px;
  .child_item {
    display: flex;
    align-items: center;

    .label {
      margin-left: 10px;
      color: white;
      font-size: 14px;
    }
  }
}
</style>