
export const styleTwo=() =>{

    addSXCity()
    addSXTrailLine()
    addSXCityStaticLine()
    centerCircle()
}

let relativeHeight = 3000

function addSXCity() {
    let id = "shanxi-city"
    let layer = global.viewer.getLayer(id)
    if (layer) {
        global.viewer.removeLayer(layer)
        global.viewer.scene.globe.show = true
        global.viewer.scene.globe.showGroundAtmosphere = true
        global.viewer.scene.skyAtmosphere.show = true

        return
    }

    const apiLayer = new DC.GeoJsonLayer(id, "json/shanxi-city.geojson", { clampToGround: true })
    const targetLayer = new DC.VectorLayer(id)

    global.viewer.addLayer(targetLayer)
    apiLayer.eachOverlay((item) => {

        if (item.polygon) {
            let polygon = DC.Polygon.fromEntity(item)
            polygon.setStyle({
                outline: false,
                extrudedHeight: 3000,
                closeTop: true,
                closeBottom: true,
                material: new DC.PolygonGradientMaterialProperty({
                    color: DC.Namespace.Cesium.Color.fromRandom().withAlpha(1.0),
                    alphaPower: 1.0,
                    diffusePower: 10.0,
                }),
                distanceDisplayCondition: {
                    near: 400000, //最近距离
                    far: Number.MAX_VALUE //最远距离
                }
            })
            targetLayer.addOverlay(polygon)
        }
    })
}

function addSXTrailLine() {
    let id = "tiandirenche-Line";
    let layer = viewer.getLayer(id)
    if (layer) {
        layer.show = !layer.show
        return
    }

    const apiLayer = new DC.GeoJsonLayer(id, "json/shanxi.geojson", { clampToGround: true })
    const targetLayer = new DC.VectorLayer(id)
    viewer.addLayer(targetLayer)
    apiLayer.eachOverlay((item) => {
        if (item.polygon) {
            let polygon = DC.Polygon.fromEntity(item)
            let pts = polygon.positions.map((postion) => {
                postion.alt += 3000
                return postion;
            })
            let polyline = new DC.Polyline(pts)
            polyline.setStyle({
                material: new DC.PolylineLightingTrailMaterialProperty({
                    color: DC.Namespace.Cesium.Color.AQUA,
                    speed: 1,
                }),
                width: 25
            })
            targetLayer.addOverlay(polyline)

        }
    })


}

function addSXCityStaticLine() {
    let id = "tiandirenche-CityLine";
    let layer = viewer.getLayer(id)
    if (layer) {
        layer.show = !layer.show
        return
    }

    const apiLayer = new DC.GeoJsonLayer(id, "json/shanxi-city.geojson", { clampToGround: true })
    const targetLayer = new DC.VectorLayer(id)
    viewer.addLayer(targetLayer)
    apiLayer.eachOverlay((item) => {
        if (item.polygon) {
            let polygon = DC.Polygon.fromEntity(item)
            let pts = polygon.positions.map((postion) => {
                postion.alt += 2500
                return postion;
            })
            let polyline = new DC.Polyline(pts)
            polyline.setStyle({
                clampToGround: true,
                material: DC.Namespace.Cesium.Color.WHITE,
                width: 3,
                zIndex: 999,
            })
            targetLayer.addOverlay(polyline)
        }
    })
}


function centerCircle() {
    let center = [108.948024, 35.663161, relativeHeight + 2000]
    let id = "centerCircle-bg";
    let layer = viewer.getLayer(id);
    if (layer) {
        layer.show = !layer.show;
        return;
    }
    const targetLayer = new DC.VectorLayer(id);

    viewer.addLayer(targetLayer);

    let circle = new DC.Circle(center, 500000)
    circle.setStyle({
        fill: false,
        extrudedHeight: relativeHeight,
        heightReference: DC.Namespace.Cesium.HeightReference.CLAMP_TO_GROUND,
        outline: true,
        outlineColor: DC.Namespace.Cesium.Color.fromCssColorString("#fff"),
        outlineWidth: 5.0,
    })
    targetLayer.addOverlay(circle)


    let innerCircle = new DC.Circle(center, 300000)
    innerCircle.setStyle({
        fill: false,
        extrudedHeight: relativeHeight,
        heightReference: DC.Namespace.Cesium.HeightReference.CLAMP_TO_GROUND,
        outline: true,
        outlineColor: DC.Namespace.Cesium.Color.fromCssColorString("#fff"),
        outlineWidth: 5.0,
    })
    targetLayer.addOverlay(innerCircle)
}

export function addMask() {
    let id = "sx_maskid";
    let layer = viewer.getLayer(id)
    if (layer) {
        layer.show = !layer.show
        return
    }

    const apiLayer = new DC.GeoJsonLayer(id, "json/sx-line.geojson", { clampToGround: true })
    const targetLayer = new DC.VectorLayer(id)
    viewer.addLayer(targetLayer)
    apiLayer.eachOverlay((item) => {
        if (item.polyline) {
            let polyline = DC.Polyline.fromEntity(item)
            let pts = polyline.positions.map((postion) => {
                postion.alt += 0
                return postion;
            })
            let mask = new DC.Polygon([
                [70, -70],
                [70, 70],
                [150, 70],
                [150, -70]
              ])
              mask.id = 'sxMask'
              mask.holes = [pts]
              mask.setStyle({
              
                material: new DC.Namespace.Cesium.Color.BLACK.withAlpha(0.5)
              })
              targetLayer.addOverlay(mask)
        }
    })
}


