import { flyPositionFromC3 } from "../../utils/fly";
import windData from "./windData";
export const addWheather = (name) => {
  if (["rain", "snow", "fog", "cloud"].indexOf(name)>-1) {
    viewer.weather[name].enable =
      !viewer.weather[name].enable;
  } else if (name === "wind") {
    addWindDCLayer(viewer);
  } else if (name == "cityWeather") {
    addCityWeather(viewer);
  } else if (name === "close") {
    ["cloud", "cityWeather", "wind", "rain", "snow", "fog"].forEach((wea) => {

      if (viewer.weather[wea]) {
        viewer.weather[wea].enable = false;
      }
    });
  }
}

export function addCityWeather(viewer) {
  const id = "cityWeather-layer"
  let layer = viewer.getLayer(id)
  if (layer) {
    viewer.removeLayer(layer)
    return
  }
  flyPositionFromC3(
    {
      x: -2161635.164441589,
      y: 6086876.588973105,
      z: 4617170.438440242,
    },
    { delay: 0 },
    [5, -90],
    () => {
      let layer = new DC.VectorLayer(id)
      viewer.addLayer(layer)
      let icons = [
        require("@/assets/weather/rain.png"),
        require("@/assets/weather/clound.png"),
        require("@/assets/weather/lightning.png"),
        require("@/assets/weather/sunny.png"),
        require("@/assets/weather/wumai.png"),
      ]
      let pts = [
        { id: "xian-polygon", center: [108.948024, 34.263161] },
        { id: "xianyang-polygon", center: [108.394642, 34.785702] },
        
        { id: "baoji-polygon", center: [107.205191, 34.380063] },
        { id: "weinan-polygon", center: [109.856208, 34.950969] },
        { id: "tongchuan-polygon", center: [109.038368, 35.195996] },
        { id: "yanan-polygon", center: [109.319393, 36.442397] },
        { id: "yulin-polygon", center: [109.577105, 38.048207] },
        { id: "hanzhong-polygon", center: [107.106739, 33.090936] },
        { id: "ankang-polygon", center: [108.929995, 32.759384] },
        { id: "shangluo-polygon", center: [109.907166, 33.647601] },
        { id: "hancheng-polygon", center: [110.428889125,35.594928718] },
        { id: "yangling-polygon", center: [108.018105781,34.293747797] },
      ]

      pts.forEach((item) => {
        if (item.center) {
          let billboard = new DC.Billboard(item.center, icons[Math.ceil(Math.random() * 4)])
          billboard.setStyle({
            heightReference: 1,
            disableDepthTestDistance: Number.POSITIVE_INFINITY,
          })
          layer.addOverlay(billboard)
        }
      })
    }
  )
}

export function addWindDCLayer(viewer) {
  const id = "wind-layer"
  let layer = viewer.getLayer(id)
  if (layer) {
    viewer.removeLayer(layer)
    // layer.show = !layer.show
    return
  }

  flyPositionFromC3(
    {
      x: -2161635.164441589,
      y: 6086876.588973105,
      z: 4617170.438440242,
    },
    { delay: 0 },
    [5, -90],
    () => {
      layer = new DC.WindLayer(id, {
        colorScale: [
          "rgb(36,104, 180)",
          "rgb(60,157, 194)",
          "rgb(128,205,193 )",
          "rgb(151,218,168 )",
          "rgb(198,231,181)",
          "rgb(238,247,217)",
          "rgb(255,238,159)",
          "rgb(252,217,125)",
          "rgb(255,182,100)",
          "rgb(252,150,75)",
          "rgb(250,112,52)",
          "rgb(245,64,32)",
          "rgb(237,45,28)",
          "rgb(220,24,32)",
          "rgb(180,0,35)",
        ],
        frameRate: 16,
        maxAge: 60,
        globalAlpha: 0.9,
        velocityScale: 1 / 30,
        paths: 3000,
        useCoordsDraw: true,
      })
      layer.setData(windData)
      viewer.addLayer(layer)
    }
  )
}
