
export const addSXCity = () => {
    let id = "shanxi-city"
    let layer = global.viewer.getLayer(id)
    if (layer) {
        global.viewer.removeLayer(layer)
        global.viewer.scene.globe.show = true
        global.viewer.scene.globe.showGroundAtmosphere = true
        global.viewer.scene.skyAtmosphere.show = true

        return
    }
    const apiLayer = new DC.GeoJsonLayer(id, "json/shanxi-city.geojson", { clampToGround: true })
    const targetLayer = new DC.VectorLayer(id)

    global.viewer.addLayer(targetLayer)


    beautyVillage(targetLayer)
    apiLayer.eachOverlay((item) => {

        if (item.polygon) {
            let polygon = DC.Polygon.fromEntity(item)
            polygon.setStyle({
                outline: false,
                extrudedHeight: 35000,
                closeTop: true,
                closeBottom: true,
                material: new DC.PolygonGradientMaterialProperty({
                    color: DC.Namespace.Cesium.Color.fromRandom().withAlpha(1.0),
                    alphaPower: 1.0,
                    diffusePower: 10.0,
                }),
                distanceDisplayCondition: {
                    near: 400000, //最近距离
                    far: Number.MAX_VALUE //最远距离
                }
            })
            targetLayer.addOverlay(polygon)
            let pts = polygon.positions.map((postion) => {
                postion.alt += 30000
                return postion;
            })
            let polyline = new DC.Polyline(pts)
            polyline.setStyle({
                material: DC.Namespace.Cesium.Color.WHITE,
                width: 3,
                distanceDisplayCondition: {
                    near: 400000, //最近距离
                    far: Number.MAX_VALUE //最远距离
                },
                disableDepthTestDistance: Number.MAX_VALUE
            })
            targetLayer.addOverlay(polyline)
            let poi = item.properties.center.getValue()
            let position = new DC.Position(poi[0], poi[1], 35000)
            let Label = new DC.Label(position, item.properties.name.getValue())
            Label.setStyle({

                font: "normal 42px 楷体",
                scale: 0.5,
                distanceDisplayCondition: {
                    near: 400000, //最近距离
                    far: Number.MAX_VALUE //最远距离
                }
            })
            targetLayer.addOverlay(Label)


            let circle = new DC.Circle(position, 30000)
            circle.setStyle({
                semiMinorAxis: 30000.0,
                semiMajorAxis: 30000.0,
                height: 35000,
                material: new DC.CircleWaveMaterialProperty({
                    color: DC.Namespace.Cesium.Color.fromRandom(),
                    count: Math.ceil(Math.random() * 3),
                    gradient: 0.2,
                    speed: 5,
                }),
                distanceDisplayCondition: {
                    near: 400000, //最近距离
                    far: Number.MAX_VALUE //最远距离
                }
            })
            targetLayer.addOverlay(circle)
        }
    })

}

function beautyVillage(targetLayer) {
    const ssi = require("@/assets/img/calib.png")
    // let rotation =  DC.Namespace.Cesium.Math.toRadians(30)
    let rect1 = new DC.Rectangle('101.0,30.0;117.0,42.0')
    rect1.setStyle({
        material: new DC.Namespace.Cesium.ImageMaterialProperty({
            image: ssi,
            transparent: true,
            color: new DC.Namespace.Cesium.Color(0.4, 0.4, 0.4, 1.0),
        }),

        height: 35000.0,
    })
    targetLayer.addOverlay(rect1)


    const ssivalue = require("@/assets/img/calib-value.png")
    // rotation =  DC.Namespace.Cesium.Math.toRadians(30)
    let rect2 = new DC.Rectangle('102.0,31.0;116.0,41.0')
    rect2.setStyle({
        material: new DC.Namespace.Cesium.ImageMaterialProperty({
            image: ssivalue,
            transparent: true,
            color: DC.Namespace.Cesium.Color.WHITE,
        }),

        height: 35000.0,
    })
    targetLayer.addOverlay(rect2)

    const ssiDir = require("@/assets/img/calib-dir.png")
    // rotation =  DC.Namespace.Cesium.Math.toRadians(30)
    let rect3 = new DC.Rectangle('102.0,31.0;116.0,41.0')
    rect3.setStyle({
        material: new DC.Namespace.Cesium.ImageMaterialProperty({
            image: ssiDir,
            transparent: true,
            color: DC.Namespace.Cesium.Color.WHITE,
        }),

        height: 35000.0,
    })
    targetLayer.addOverlay(rect3)


}