// import ViewShedStage from "./ViewShedStage";
export const sunAny = {
    start: () => {
        viewer.delegate.scene.globe.enableLighting = true;
        viewer.delegate.shadows = true;
        viewer.delegate.clock.multiplier = 1600;
    },
    stop: () => {
        viewer.delegate.scene.globe.enableLighting = false;
        viewer.delegate.shadows = false;
        viewer.delegate.clock.multiplier = 1;
    }
}
export const contourLine = {
    start: () => {
        let globe = viewer.delegate.scene.globe;
        let contourUniforms = {};
        // 使用等高线材质
        let material = DC.Namespace.Cesium.Material.fromType("ElevationContour");
        contourUniforms = material.uniforms;
        // 线宽2.0px
        contourUniforms.width = 2.0;
        // 高度间隔为150米
        contourUniforms.spacing = 150;
        contourUniforms.color = DC.Namespace.Cesium.Color.RED;
        // 设置材质
        globe.material = material;
    },
    stop: () => {
        viewer.delegate.scene.globe.material = null;
    }
}
let viewShed = {}
let startPosition=null     //起始坐标
let endPosition= null       //终点坐标
let viewShedArr= []        //存储可视域区域的数组
let horizontalViewAngle = 90  //视角水平张角
let verticalViewAngle = 60    //视角垂直张角
let handler=null
export const viewAny = {
    start: () => {
     
        let i = 0
        handler = new DC.Namespace.Cesium.ScreenSpaceEventHandler(viewer.scene.canvas)
        handler.setInputAction(movement =>
        {
          i++
          if(i === 1)
          {
             startPosition = viewer.scene.pickPosition(movement.position) //鼠标点击一次获取开始坐标
            if(!startPosition) return
            viewShed = new DC.analysis.ViewShedStage(viewer.delegate,{
              viewPosition: startPosition,
              viewPositionEnd: startPosition,
              horizontalViewAngle: horizontalViewAngle,
              verticalViewAngle: verticalViewAngle
            })
            handler.setInputAction(movement => //鼠标移动的事件
              {
                endPosition =  viewer.scene.pickPosition(movement.endPosition)
                if(!endPosition) return
                viewShed.updatePosition(endPosition)
                if(!viewShed.sketch) {
                  viewShed.drawSketch()
                }
              }, DC.Namespace.Cesium.ScreenSpaceEventType.MOUSE_MOVE)         
          }
          if(i === 2) //鼠标点击两次获取结束坐标
          { 
            i = 0
            endPosition =  viewer.scene.pickPosition(movement.position)
            viewShed.updatePosition(endPosition)
            viewShed.update()
            handler = handler && handler.destroy()  //销毁鼠标事件
            viewShedArr.push(viewShed)
          } 
        }, DC.Namespace.Cesium.ScreenSpaceEventType.LEFT_CLICK)
    },
    stop: () => {
        if(viewShed){
            viewShed.clear()
        }
    }
}
