// 厂区
export function addChangQu(id, url) {
    createTileLayer(id, url, (tileset) => {
        let position = new DC.Position(108.496711111, 34.28423329999998)
        tileset.setPosition(position)
        tileset.setHeight(411, false)
        viewer.flyTo(tileset)
    })
}
// 居民楼
export function addJml(id, url) {
    createTileLayer(id, url, (tileset) => {
        viewer.flyTo(tileset)
    })
}
function createTileLayer(id, url, callback) {
    let layer = viewer.getLayer(id)
    if (layer) {
        layer.show = true
        viewer.flyTo(viewer.getLayer(id).getOverlays()[0])
        return
    }
    layer = new DC.TilesetLayer(id)
    viewer.addLayer(layer)
    let tileset = new DC.Tileset(url)
    layer.addOverlay(tileset)

    callback(tileset)
}